

  
  .newProductContainer > form > h1 {
    text-align: center;
    font: 400 2.5vmax "Roboto";
    text-decoration: underline;
    margin-bottom: 2rem;
    color: rgba(0, 0, 0, 0.687);
  }
  .newProductContainer > form  {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    transition: all 0.5s;
  }
  .newProductContainer > form > div {
    display: flex;
    width: 25vw;
    align-items: center;
  }
  
  .newProductContainer > form > div > input,
  .newProductContainer > form > div > textarea,
  .newProductContainer > form > div > select {
    margin: 0.5vmax 0;
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
  }
  .newProductContainer > form > div > select > option {
    font: 400 1.2vmax "Roboto";
  }
  
  .newProductContainer > form > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.687);
  }
  #createNewProductImage > input{
      display: flex;
      padding: 0;
  
  }
  #createNewProductImage > input::file-selector-button{
     
      cursor: pointer;
      width: 100%;
      z-index: 2;
      height: 5vh;
      border: none;
      margin: 0;
      font: 400 0.8vmax cursive;
      transition: all 0.5s;
      padding: 0 1vmax;
      color: rgba(0, 0, 0, 0.623);
      background-color: #fff;
  
  }
  
  #createNewProductImage > input::file-selector-button:hover{
      background-color: rgb(230, 225, 225);
  }
  
  #createNewProductImagePreview::-webkit-scrollbar {
    width: none!important;
  }
  #createNewProductImagePreview{
      height: 5vmax;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items:center ;
      overflow-x:auto;
  
  }
  #createNewProductImagePreview>img{
      height: 90%;
      width: 6vmax;
      margin-inline: 5px;
  }
  
  #createProductBtn{
      width: 100%;
  }
  
  @media screen and (max-width: 600px) {
  
  .newProductContainer > form > h1 {
  
      font: 400 3vmax "Roboto";
  
  }
  
  .newProductContainer > form{
  
      padding: 0;
  
  }
  .newProductContainer > form > div {
      width: 80vw;
  }
  
  
  .newProductContainer > form > div > input,
  .newProductContainer > form > div > textarea,
  .newProductContainer > form > div > select {
    margin: 0.5vmax 0;
    padding: 2vmax 6vmax;
    font: 300 2vmax cursive;
  }
  
  
  .newProductContainer > form > div > select > option {
      font: 400 2vmax "Roboto";
    }
    
    .newProductContainer > form > div > svg {
     
      transform: translateX(1.5vmax);
      font-size: 3vmax;
      color: rgba(0, 0, 0, 0.687);
    }
    
    #createNewProductImage > input::file-selector-button{
        font: 400 2vmax cursive;
    }
  
    
  }
  